@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');
.App {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Outfit", sans-serif;
    padding-bottom: 30px;
}
.App > form {
    width: 40%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
    padding: 30px;
}
.App h2 {
    font-size: 24px;
    font-weight: 500;
    align-self: flex-start;
}
.Field {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    width: 100%;
}
.Field label {
    font-size: 16px;
    font-weight: 400;
}
.Field label sup {
    color: red;
}
.Field input, .Field select {
    height: 40px;
    width: 100%;
    background-color: #EDF5F4;
    border-color: transparent;
    border-radius: 8px;
    padding: 0px 8px;
    font-family: "Outfit", sans-serif;
    font-weight: 500;

}
.Field input::placeholder {
    font-family: "Outfit", sans-serif;
}
.Field input::-ms-value {
    font-family: "Outfit", sans-serif;
}
button:enabled {
    width: 100%;
    height: 40px;
    background-color: black;
    border-color: transparent;
    border-radius: 8px;
    color: white;
    font-family: "Outfit", sans-serif;
}
button:disabled {
    width: 100%;
    height: 40px;
    background-color: rgba(0, 0, 0, 0.219);
    border-color: transparent;
    border-radius: 8px;
    color: white;
    font-family: "Outfit", sans-serif;
} 
.FieldError {
    color: red;
    font-size: 14px;
}